@import "../../globalSASS/global.scss";
.nav {
    position: relative;
    width: 100%;
    height: 100%;

    font-family: 'VinqueRg-Regular', serif;

    &__burger {
        display: none;
        position: absolute;
        top: 5%;
        left: 5%;
        width: 40px;
        height: 40px;

        cursor: pointer;

    }

    &__cancel {
        display: none;
        position: absolute;
        top: 5%;
        left: 5%;
        width: 40px;
        height: 40px;

        cursor: pointer;
    }

    &__mobile {
        display: none;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-color: rgba($color: #000000, $alpha: 0.8);
    }

    &__item {
        position: absolute;

        font-size: 2.5em;
        text-align: center;

        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.7);

        &_video {
            top: 13.2%;
            left: 13.1%;
            letter-spacing: 0.04em;
        }
        
        &_hall {
            top: 12.6%;
            right: 13.8%;
            line-height: 1em;
            letter-spacing: 0.05em;
        }

        &_group {
            top: 26.9%;
            left: 8.3%;
            letter-spacing: 0.1em;
        }

        &_contacts {
            top:26.5%;
            right: 8.3%;
            letter-spacing: 0.035em;
        }
    }

    &__link {
        text-decoration: none;
        color: rgba(209, 205, 205, 0.7);

        &_mob {
            font-size: 20px;
            text-align: center;
            margin-bottom: 30px;
            color: #c4c4c4;
            text-shadow: 0px 4px 4px rgba($color: rgb(82, 82, 82), $alpha: 1.0);
        }
    }

    @media screen and (max-width:1680px) {
        font-size: 14px;
    }

    @media screen and (max-width: 1500px){
        .nav__item {

            &_video {
                left: 10%;
            }
        }
    }

    @media screen and (max-width:1400px) {
        .nav__item {
    
            font-size: 2.2em;
    
            &_video {
                left: 8%;
            }
            &_hall {
                right: 13.8%;
            }
    
            &_group {
                left: 5%;
            }
    
            &_contacts {
                right: 9%;
            }
        }
    }
    @media screen and (max-width:1400px) {
        .nav__item {
            font-size: 1.8em;}
    }

    @media screen and (max-width:900px){
        .nav {
            &__item {
                font-size: 1.5em;

                &_video {
                    left: 5%;
                }
                &_hall {
                    right: 8%;
                }
        
                &_group {
                    left: 3%;
                }
        
                &_contacts {
                    right: 5%;
                }
            }
           
        }
    }
    @media screen and (max-width:767px) {
        .nav {
            &__item {
                display: none;
            }
            &__burger {
                display: block;
            }
        }
    }

}

