@import "../../globalSASS/global.scss";
.footer {
    display: flex;
    align-items: center;
    width: 94%;
    margin-left: auto;
    margin-right: auto;

    &_pos {
        margin-top: 7.4%;
        padding-bottom: 2%;
    }

    
    &__doc {
        font-size: 1.25em;
        text-decoration: none;
        color: rgba(255, 246, 246, 0.7);
        letter-spacing: 0.038em;

        &:hover {
            text-decoration: underline;
        }
        margin-right: 8.8%;
    }

    &__contacts {
        display: flex;
        flex-direction: column;
    }

    &__text {
        font-size: 1.25em;
        text-transform: uppercase;
        text-decoration: underline;
        color: rgba(255, 246, 246, 0.7);

        margin-bottom: 8px;
    }

    &__link {
        font-family: 'CInzel(RUS BY LYAJKA)', serif;
        text-decoration: none;
        color: rgba(255, 246, 246, 0.7);
        font-size: 1.25em;
        text-transform: uppercase;
        letter-spacing: -0.02em;
        line-height: 100%;
        
        &:hover {
            text-decoration: underline;
        }

        &_short {
            display: none;
        }
    }

    &__logo {
        font-family: 'CInzel(RUS BY LYAJKA)', serif;
        font-size: 2.5em;
        text-decoration: underline;
        color: rgba(255, 246, 246, 0.7);
        margin-left: 4.9%;
    }

    @media screen and (max-width: 1900px) {
        font-size: 14px;
    }

    @media screen and(max-width: 1660px) {
        
        justify-content: space-between;

        .footer {

            &__doc {
                margin: 0;
            }

            &__logo {
                margin: 0;
            }
        }
    }

    @media screen and (max-width:1400px) {
        font-size: 12px;
    }

    @media screen and (max-width:1180px) {
        font-size: 10px;
    }

    @media screen and(max-width:1040px) {
        font-size: 8px;

        .footer {
            &__doc {
                display: flex;
                width: 140px;
            }
        }
    }
    @media screen and(max-width:767px) {
        .footer {
            &__logo {
                display: none;
            }

            &__doc {
                &_conf {
                    display: none;
                }
            }
        }
    }
    
}