@import "../../globalSASS/global.scss";
.price {
	font-size: 1.5625em;
	font-family: Cinzel, sans-serif;
	color: #FFF7F7;
	text-shadow: 3px 5px 5px rgba(0, 0, 0, 1);
	width: 45%;
	margin-top: 3.3%;

	&__row {
		display: flex;
		align-items: center;
		margin-top: 5%;

		&_title {
			margin-top: 0;
			margin-bottom: 8%;
		}
	}

	&__sector {
		display: flex;
		min-width: 262px;
	}

	&__cost {
		min-width: 207px;

		margin-left: 13.8%;
	}

	&__button {
		font-family: 'CInzel(RUS BY LYAJKA)', serif;

		width: 140px;
		height: 23px;

		background: rgba(196, 196, 196, 0.2);
		// -webkit-filter: drop-shadow (0px 3px 3px rgba(0, 0, 0, 1));
		filter: drop-shadow(0px 5px 3px rgba(0, 0, 0, 1));
		border-radius: 30px;
		border: none;

		font-size: 15px;
		color: #FFF6F6;
		margin-left: 7%;

		cursor: pointer;
	}

	@media screen and (max-width: 1850px) {
		font-size: 1.1em;

		.price {

			&__sector {
				min-width: 180px;
			}


		}
	}

	@media screen and (max-width: 1680px) {
		width: 40%;

		.price {

			&__cost {
				min-width: 160px;
				margin-left: 8%;
			}

			&__button {
				margin-left: 5%;
			}
		}
	}

	@media screen and (max-width:1400px) {
		width: 45%;
	}

	@media screen and (max-width:1320px) {
		.price__row {
			margin-top: 2%;

			&_title {
				margin-bottom: 4%;
			}
		}
	}

	@media screen and (max-width:1230px) {
		width: 50%;
	}

	@media screen and (max-width:1100px) {
		font-size: 1em;
		width: 46%;

		.price {
			&__sector {
				min-width: 150px;
			}

			&__cost {
				min-width: 130px;
				margin-left: 8%;
			}

			&__button {
				margin-left: 5%;
			}
		}
	}

	@media screen and (max-width: 1040px) {
		width: 70%;

		.price {
			&__cost {
				margin-left: 0;
			}

			&__button {
				margin-left: 0;
			}

			&__row {
				width: 100%;
				justify-content: space-between;
			}
		}
	}

	@media screen and (max-width: 767px) {
		min-width: 300px;
		.price {
			&__sector {
				display: none;
			}
		}
	}


}
