@import "../../globalSASS/global.scss";
.bg-clouds {
    position: absolute;
    bottom: 0;

    height: 75%;
    width: 100%;


    background: linear-gradient(180deg, #010101 8.23%, rgba(255, 255, 255, 0) 100%), url(img/sky.jpg);
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    
    @media screen and (max-width: 767px) {
        height: 85%;
    }
}