@import "../../globalSASS/global.scss";
.video {
	display: flex;
	justify-content: center;
	margin-top: 6.1%;


	&__img {
        width: 80.5%;
        height: 68.5vh;
    }
    cursor: pointer;

    @media screen and (max-width:1285px) {

		.video {
			&__img {
				height: 50vh;
			}
		}
    }
    
    @media screen and (max-width:940px) {

		.video {
			&__img {
				height: 30vh;
			}
		}
	}
	@media screen and (max-width:767px) {

		.video {
			&__img {
				width: 100%;
			}
		}
    }
    
    @media screen and (max-width:940px) {

		.video {
			&__img {
				height: 25vh;
			}
		}
	}
}
