@import "../../globalSASS/global.scss";
.buying {
    display: flex;
    justify-content: space-between;
    width: 95%;

    background: rgba(193, 193, 193, 0.15);

    margin-left: auto;
    margin-right: auto;

    &_pos {
        margin-top: 4.9%;
    }

    @media screen and (max-width: 1040px) {
        flex-direction: column;
        justify-content: start;
        align-items: center;
        padding-bottom: 30px;
    }
}