@import "../../globalSASS/global.scss";
.group {

	&__description {

		color: #E5E5E5;
		font-size: 30px;
		text-align: center;
		text-indent: 8px;
		line-height: 1.37em;
		letter-spacing: 0.072rem;

		margin: 0;

		margin-top: 1.6em;


		@media screen and (max-width: 1900px) {
			font-size: 25px;
        }
        
        @media screen and (max-width: 1595px) {
            letter-spacing: normal;
            
		}
		
		@media screen and (max-width: 1500px) {
			font-size: 22px;
		}

		@media screen and (max-width:1320px) {
			font-size: 20px;
		}


		&_accent {
			font-family: "CInzel(RUS BY LYAJKA)", serif;
			font-size: 1.3em;
			text-decoration: underline;
			letter-spacing: 0em;
		}
	}

	&__preview {
		display: flex;
		justify-content: space-between;

		width: 100%;

		margin-top: 4.6%;
	}

	&__img {
		width: calc(100%/5 - 2.3%);
	}

	@media screen and (max-width:767px){
		.group {
			
			&__preview {
				display: none;
			}
		}
	}

}
