@import "../../globalSASS/global.scss";
.tours {
    display: flex;
    flex-direction: column;
    color: #121515;
    width: 100%;

    &_pos{
        margin-top: 5.1%;
    }

    &__title {
        font-family: "VinqueRg-Regular", serif;
        font-size: 4.0625em;
        text-align: center;

        margin: 0;
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    @media screen and (max-width: 1680px) {
        font-size: 14px;
    }

    @media screen and (max-width: 1040px) {
        font-size: 12px;

        .tours__wrap {
            flex-direction: column;
            align-items: center;;
        }
    }

    @media screen and (max-width:767px) {
        font-size: 8px;
    }
   
}