@import "../../globalSASS/global.scss";
.more {
	width: 38.5%;
	font-size: 16px;

	&_pos {
		margin-top: 4.3%;
	}

	&__title {
		font-size: 1.875em;
		font-weight: normal;
		letter-spacing: 0.05em;
		margin: 0;
	}

	&__socials {
		margin-top: 8%;
	}

	&__social {
		display: flex;
		align-items: center;
		margin-top: 2.3em;
	}

	&__img {
		width: 1.3em;
		height: 1.3em;
	}

	&__link {
		text-decoration: none;
		color: #121515;
		font-size: 1.5625em;

		margin-left: 1.9em;

		&:hover {
			text-decoration: underline;
		}

		&:active {
			text-decoration: underline;
		}
	}

	@media screen and (max-width: 1100px) {
		font-size: 14px;
	}

	@media screen and (max-width: 1040px) {
		display: flex;
		flex-direction: column;
		align-items: center;

		font-size: 16px;

		.more {

			&__socials {
                margin-top: 2%;
            }

			&__social {
				margin-top: 1.6em;
			}

			&__link {
				margin-left: 1em;
			}
		}
    }
    
    @media screen and (max-width:767px) {
        display: none;
    }

}
